import './Links.scss';
import $ from 'jquery';
import { useEffect } from 'react';
import { db } from './resources/Firebase';
import { Capitalize } from './resources/Helpers'
import { streamer } from './resources/Config'
import { datosFirebaseUsuario } from './resources/Firebase';

const Links = () => {
    useEffect(() => {
        $("#buttons").empty()
        db.collection("users").doc(streamer).collection("buttons").orderBy("timestamp").get()
        .then((buttons) =>{
            if (buttons) {
                buttons.forEach((network) => {
                    network = network.data()
                    $("#buttons").append(
                        `<div class="row">
                            <a rel="noreferrer" class="boton" onclick={visitas('${network.name}')} href="${network.url}" target="_blank">
                                <div class="col">
                                    <img alt="${network.icon}" src="./img/${network.icon}.png" class="icon" /><span class="boton-name">${Capitalize(network.name)}</span>
                                </div>
                            </a>
                        </div>`
                    )
                })
            }
        })
        datosFirebaseUsuario()
        .then((data) => {
            $('#nota').html(data.note);
        })
    });

    return (
        <>
            <div className="row">
                <div className="nota" id="nota"></div>
            </div>
            <div id="buttons"></div>
        </>
    )
}

export default Links;